import { Component, OnInit } from '@angular/core';
import { ActivatedRoute } from '@angular/router';
import { ImageService } from '../../image.service';

@Component({
  selector: 'app-article',
  templateUrl: './article.component.html',
  styleUrls: ['./article.component.scss']
})
export class ArticleComponent implements OnInit {
  articleId: string;

  constructor(private _route: ActivatedRoute, private _imagepath: ImageService) { 
    this._route.params.subscribe(params => this.articleId = params.id);
  }

  ngOnInit() {
  }

  getImage(id: number): string {
    let path: string;
    this._imagepath.getImagepath(id).subscribe(imagepaths => path = imagepaths);
    return 'image/' + path;
  }
}
