import { Component, OnInit } from '@angular/core';
import { ImageService } from '../../../image.service';
import { Router } from '@angular/router';

@Component({
  selector: 'app-about-ecotourism',
  templateUrl: './about-ecotourism.component.html',
  styleUrls: ['./about-ecotourism.component.scss']
})
export class AboutEcotourismComponent implements OnInit {

  constructor(private _imagepath: ImageService, private _router: Router) { }

  ngOnInit() {
  }

  routeTo() {
    this._router.navigate(['ecotourism/aboutSerengeti']);
    window.scroll(0,0);
  }

  getImage(id: number): string {
    let path: string;
    this._imagepath.getImagepath(id).subscribe(imagepaths => path = imagepaths);
    return 'image/' + path;
  }
}
