import { NgModule } from '@angular/core';
import { Routes, RouterModule } from '@angular/router';
import { NotfoundComponent } from './components/common/notfound/notfound.component';
import { EcotourismComponent } from './ecotourism.component';
import { LandingComponent } from './components/landing/landing.component';
import { ArticleComponent } from './components/article/article.component';
import { ProgramComponent } from './components/static-pages/program/program.component';
import { AnnouncementsComponent } from './components/announcements/announcements.component';
import { ArticlesComponent } from './components/articles/articles.component';
import { AnnouncementComponent } from './components/announcement/announcement.component';
import { RouterLinkType} from '@app/enums';

import {
	AboutEcotourismComponent,
	AboutSerengetiComponent,
	AboutUsComponent
} from './components/static-pages'

import { ProgramsComponent } from './components/static-pages/programs/programs.component';
import { MainComponent } from './components/main/main.component';


const ecotourismAppRoutes: Routes = [  
    { 
		path: RouterLinkType.BLANK, 
		component: EcotourismComponent, 
		children: [
			// {
			// 	path: RouterLinkType.MAIN,
			// 	component: MainComponent
			// },
			{ 
				path: RouterLinkType.LANDING,
				component: LandingComponent 
			},
			{ 
				path: RouterLinkType.ABOUT_US, 
				component: AboutUsComponent 
			},
			{ 
				path: RouterLinkType.ABOUT_SERENGETI,
				component: AboutSerengetiComponent 
			},
			{ 
				path: RouterLinkType.ABOUT_ECOTOURISM, 
				component: AboutEcotourismComponent 
			},
			{ 
				path: RouterLinkType.PROGRAMS, 
				component: ProgramsComponent 
			},
			{ 
				path: RouterLinkType.PROGRAM_ITEMS, 
				component: ProgramComponent 
			},
			{ 
				path: RouterLinkType.ANNOUNCEMENTS, 
				component: AnnouncementsComponent 
			},
			{ 
				path: RouterLinkType.ANNOUNCEMENT_ITEMS, 
				component: AnnouncementComponent 
			},
			{ 
				path: RouterLinkType.ARTICLES, 
				component: ArticlesComponent 
			},
			// { 
			// 	path: RouterLinkType.ARTICLE_ITEMS, 
			// 	redirectTo: RouterLinkType.ARTICLES, 
			// 	pathMatch: 'full' 
			// },
			{ 
				path: RouterLinkType.ARTICLE_ITEMS, 
				component: ArticleComponent 
			},
			{ 
				path: RouterLinkType.WILDCARD, 
				component: NotfoundComponent 
			}
		]
	}
];

@NgModule({
  imports: [RouterModule.forChild(ecotourismAppRoutes)],
  exports: [RouterModule]
})
export class EcotourismAppRoutingModule { }
