import { Component, OnInit } from '@angular/core';
import { Router } from '@angular/router';
import { DataService } from '../../data.service';
import { ImageService } from '../../image.service';

const mockup = {
	data: [
		{
			id: 1,
			contents: [
				{ id:34 },
				{ id:35 },
				{ id:36 },
				{ id:37 },
				{ id:91 },
				{ id:92 },
				{ id:93 },
				{ id:94 }
			]
		}
	]
}

@Component({
	selector: 'app-articles',
	templateUrl: './articles.component.html',
	styleUrls: ['./articles.component.scss']
})
export class ArticlesComponent implements OnInit {
	articles;	

	constructor(private _router: Router, private _data: DataService, private _imagepath: ImageService) { }

	ngOnInit() {
		this.articles = mockup.data;
	}

	getImage(id: number): string {
		let path: string;
		this._imagepath.getImagepath(id).subscribe(imagepaths => path = imagepaths);
		return 'image/' + path;
	}

	goTo(url: string): void {
		console.log('mark', this._router.url, url);
		this._router.navigate(["" + this._router.url +'/'+ url]);
	}

	goToArticle(id) {
		this._router.navigate(["" + this._router.url +'/'+ id]);
	}
}
