import { Component, OnInit } from '@angular/core';
import { ActivatedRoute } from '@angular/router';
import { ImageService } from '../../image.service';

@Component({
  selector: 'app-announcement',
  templateUrl: './announcement.component.html',
  styleUrls: ['./announcement.component.scss']
})
export class AnnouncementComponent implements OnInit {
  announcementId: string;

  constructor(private _route: ActivatedRoute, private _imagepath: ImageService) {
    this._route.params.subscribe(params => this.announcementId = params.id);
  }

  ngOnInit() {
  
  }

  getImage(id: number): string {
    let path: string;
    this._imagepath.getImagepath(id).subscribe(imagepaths => path = imagepaths);
    return 'image/' + path;
  }
}
