import { Component, OnInit } from '@angular/core';
import { ImageService } from '../../image.service';
import { Router } from '@angular/router';

const mockup = {
	data: [
		{ id:34 },
		{ id:35 },
		{ id:36 },
		{ id:37 },
		{ id:91 },
		{ id:92 },
		{ id:93 },
		{ id:94 }
  	]
}

@Component({
	selector: 'app-landing',
	templateUrl: './landing.component.html',
	styleUrls: ['./landing.component.scss']
})
export class LandingComponent implements OnInit {
	cards;

	constructor(private _imagepath: ImageService, private _router: Router) { }

	ngOnInit() {
		this.cards = mockup.data;
	}

	goTo(url: string): void {
		this._router.navigate(["" + this._router.url +'/'+ url]);
	}

	getImage(id: number): string {
		let path: string;
		this._imagepath.getImagepath(id).subscribe(imagepaths => path = imagepaths);
		return 'image/' + path;
	}
}
