import { Component, OnInit } from '@angular/core';
// import { ActivatedRoute, Router } from '@angular/router';

import { ImageService } from './image.service';
import { ActivatedRoute, Router } from '@angular/router';

@Component({
	selector: 'app-ecotourism',
	templateUrl: './ecotourism.component.html',
	styleUrls: ['./ecotourism.component.scss']
})
export class EcotourismComponent implements OnInit {
	projectName = "ecotourism";
	isMain: boolean;

	constructor(
		private _router: Router,
		private _imagepath: ImageService,
		private _route: ActivatedRoute
	) { }

	ngOnInit() { 
	}

	goTo(url: string): void {
		this._router.navigate([this.projectName + url]);
	}

	getImage(id: number): string {
		let path: string;
		this._imagepath.getImagepath(id).subscribe(imagepaths => path = imagepaths);
		return 'image/' + path;
	}
}
