import { Injectable } from '@angular/core';
import { Imagepath } from 'src/assets/images/imagepath';
import { Observable, of } from 'rxjs';
import { IMAGEPATH } from 'src/assets/images/mock-imagepath';

@Injectable({
  providedIn: 'root'
})
export class ImageService {

	constructor() { }

	getImagepath(id: number): Observable<string> {    
		return of(IMAGEPATH.find(image => image.id == id).path);
	}
}
