import { Injectable } from '@angular/core';
import { Project } from 'src/assets/mockups/project/project';
import { BehaviorSubject } from 'rxjs';

@Injectable({
  providedIn: 'root'
})
export class DataService {
  private _projectSource = new BehaviorSubject(null);
  private _announcementSource = new BehaviorSubject(null);
  currentMessage = this._projectSource.asObservable();

  constructor() { }

  changeProject(project: Project) {
    this._projectSource.next(project);
  }
}
